*{
    margin: 0;
    padding: 0;
    border: 0;
    outline: 0;
    box-sizing: border-box;
    list-style: none;
    text-decoration: none;
}

:root{
    --color-bg:#1f1f38;
    --color-bg-variant:#2c2c6c;
    --color-primary:#4db5ff;
    --color-primary-variant:rgba(77,181,255,0.4);
    --color-white:#fff;
    --color-ligt:rgba(255,255,255,0.6);
    --transition: all 500ms case ;

    --container-width-lg:75%;
    --container-width-md:86%;
    --container-width-sm:90%;
}

html{
    scroll-behavior: smooth;
}

::-webkit-scrollbar{
    display: none;
}

body{
    font-family: 'Poppins', sans-serif;
    background: #1f1f38;
    background: var(--color-bg);
    color: #fff;
    color: var(--color-white);
    line-height: 1.7;
    background-image: url(data:image/png;base64,);

}

/*---------------- Some Genrel CSS---------------*/

.container{
    width: 75%;
    width: var(--container-width-lg);
    margin: 0 auto;
}
h1,h2,h3,h4,h5{
    font-weight: 300;
}
h1{
    font-size: 2.5rem;
}

section{
    margin-top: 8rem;
    
}

section > h2 , section > h5 {
    text-align: center;
    color: var(--color-light);
}

section > h2 {
    color: #4db5ff;
    color: var(--color-primary);
    margin-bottom: 3rem;
}

.text-light{
    color: var(--color-light);
}

a{
    color: #4db5ff;
    color: var(--color-primary);
    transition: all 500ms case;
    transition: var(--transition);
}

a:hover{
    color: #fff;
    color: var(--color-white);
}

.btn{
    width: -webkit-max-content;
    width: max-content;
    display: inline-block;
    color: #4db5ff;
    color: var(--color-primary);
    padding: 0.75rem 1.2rem;
    border-radius: 0.4rem;
    cursor: pointer;
    border: 1px solid #4db5ff;
    border: 1px solid var(--color-primary);
    transition: all 500ms case;
    transition: var(--transition);
}

.btn:hover{
    background: #fff;
    background: var(--color-white);
    color: #1f1f38;
    color: var(--color-bg);
    border: transparent;
}

.btn-primary{
    background: #4db5ff;
    background: var(--color-primary);
    color: #1f1f38;
    color: var(--color-bg);
}

img{
    display: block;
    width: 100%;
    object-fit: cover;
}

/*-------------------------- Media for Tablets---------------------------*/

@media screen and (max-width:1024px) {
    .container{
        width: 86%;
        width: var(--container-width-md);
    }

    .section{
        margin-top: 6rem;
    }
}

/*-------------Mediam for Mobile--------------*/
@media screen and (max-width:600px) {
    .container{
        width: 90%;
        width: var(--container-width-sm);
    }

    .section{
        margin-bottom: 2rem;
    }
}

header{
    height: 100vh;
    padding-top: 7rem;
    overflow: hidden;
}

.header__container{
    text-align: center;
    height: 100%;
    position: relative;
}

/*-------------- CTA styles --------------------- */
.cta{
    margin-top: 2.5rem;
    display: flex;
    grid-gap: 1.2rem;
    gap: 1.2rem;
    justify-content: center;
}

/*-------------- HeaderSocials styles --------------------- */
.header__socials{
    display: flex;
    flex-direction: column;
    align-items: center;
    grid-gap: 0.8rem;
    gap: 0.8rem;
    position: absolute;
    left: 0;
    bottom: 3rem;
}

.header__socials::after{
    contain: "";
    width: 2px;
    height: 2rem;
    background-color: var(--color-primary);
}

/*-------------- Image ME style --------------------- */
.me{
    background: linear-gradient(var(--color-primary),transparent);
    width: 22rem;
    height: 32rem;
    position: absolute;
    left: calc(50% - 11rem);
    margin-top: 4rem;
    border-radius: 12rem 12rem 0 0;

}

.scrollDown{
    position: absolute;
    right: -2.3rem;
    bottom: 5rem;
    -webkit-transform: rotate(90deg);
            transform: rotate(90deg);
}

/*-------------------------- Media for Tablets---------------------------*/

@media screen and (max-width:1024px) {
    header{
        height: 80vh;
    }
}

/*-------------------------- Media for Mobiles---------------------------*/

@media screen and (max-width:600px) {
    header{
        height: 100vh;
    }
     .header__socials,.scrollDown{
        display: none;
     }
}
nav{
    background: rgba(0, 0, 0,0.3);
    width:-webkit-max-content;
    width:max-content;
    display: block;
    padding: 0.75rem 1.7rem;
    z-index: 2;
    position: fixed;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    bottom: 2rem;
    display: flex;
    grid-gap: 0.8rem;
    gap: 0.8rem;
    border-radius: 3rem;
    -webkit-backdrop-filter: blur(15px);
            backdrop-filter: blur(15px);
}

nav a{
    background: transparent;
    padding: 0.9rem;
    border-radius: 50%;
    display: flex;
    color: var(--color-light);
    font-size: 1.1rem;
}

nav a:hover{
    background: rgba(0, 0, 0,0.3);
}

nav a.active{
    background: var(--color-bg);
    color: var(--color-white);
}
.about__container{
    display: grid;
    grid-template-columns: 35% 50%;
    grid-gap: 15%;
    gap: 15%;
}

.about__me{
    width: 100;
    aspect-ratio: 1.5/2;
    border-radius: 2rem;
    background: linear-gradient(45deg,transparent,var(--color-primary),transparent);
    display: grid;
    place-items: center;

}

.about__me-img{
    border-radius: 2rem;
    aspect-ratio: 1.5/2;
    overflow: hidden;
    -webkit-transform: rotate(10deg);
            transform: rotate(10deg);
    transition: var(--transition);
}

.about__me-img:hover{
    -webkit-transform: rotate(0);
            transform: rotate(0);
}
.about__cards{
   display: grid;
   grid-template-rows: repeat(3,1fr);
   grid-gap: 1.5rem;
   gap: 1.5rem;
}
.about__card{
    background: var(--color-bg-variant);
    border: 1px solid transparent;
    border-radius: 1rem;
    padding: 1rem;
    text-align: center;
}

.about__card:hover{
    background: transparent;
    border-color: var(--color-primary-variant);
    cursor: default;
}

.about__icon{
    color: var(--color-primary);
    font-size: 1.4rem;
    margin-bottom: 1rem;
}


/*-------------------------- Media for Tablets---------------------------*/

@media screen and (max-width:1024px) {
    .about__container{
        grid-template-columns: 1fr;
        grid-gap: 0;
        gap: 0;
    }
    .about__me{
        width: 50%;
        margin: 2rem auto 4rem;
        
    }
    .about__containt p{
        margin: 1rem 0 1.5rem;
    }
   
}

/*-------------------------- Media for Mobiles---------------------------*/

@media screen and (max-width:600px) {
    .about__me{
        width: 65%;
        margin: 0 auto 3rem;
    }

    .about__card{
        grid-template-columns: 1fr 1fr;
        grid-gap:1rem;
        gap:1rem;

    }
    .about__containt{
        text-align: center;
    }
    .about__containt p{
        margin: 1.5rem 0;
    }
}
.experience_container{
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 2rem;
    gap: 2rem;
}

.experience_container >div {
    background: var(--color-bg-variant);
    padding: 2.5rem 5rem;
    border-radius: 2.5rem;
    border: 1px solid transparent;
    transition: var(--transition);
}

.experience_container >div:hover {
    background: transparent;
    border: 3px solid;
    border-color: var(--color-primary-variant);
    cursor: default;
}

.experience_container >div h3{
    text-align: center;
    margin-bottom: 2rem;
    color: var(--color-primary);
    font-weight: 400;
}

.experience_content{
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-row-gap: 2rem;
    row-gap: 2rem;
}

.experience_details{
    display: flex;
    grid-gap: 1rem;
    gap: 1rem;
}

.experience_details-icon{
    margin-top: 6px;
    color: var(--color-primary);
}

/*-------------------------- Media for Tablets---------------------------*/

@media screen and (max-width:1024px) {

    .experience_container{
        grid-template-columns: 1fr;
    }

    .experience_container >div {
        width: 80%;
        padding: 2rem;
        margin: auto;
    }

.experience_content{
    padding: 1rem;
}

    
}

/*-------------------------- Media for Mobiles---------------------------*/

@media screen and (max-width:600px) {

    
    .experience_container {
        grid-gap: 1rem;
        gap: 1rem;
    }

    
    .experience_container >div {
        width: 100%;
        padding: 2rem 1rem;
        margin: auto;
    }

}

