.contact_container{
    width: 58%;
    display: grid;
    grid-template-columns: 30% 58%;
    grid-gap: 12%;
    gap: 12%;
}
.contact_options{
    display: flex;
    flex-direction: column;
    grid-gap: 1.2rem;
    gap: 1.2rem;
}

.contact_option{
    background: var(--color-bg-variant);
    padding: 1.2rem;
    border-radius: 1.2rem;
    text-align: center;
    border: 1px solid transparent;
    transition: var(--transition);
}

.contact_option:hover{
    background: transparent;
    border-color: var(--color-primary-variant);
}

.contact_option-icon{
    font-size: 1.5rem;
    margin-bottom: 0.5rem;
}
.contact_option a{
    margin-top: 0.7rem;
    display: inline-block;
    font-size: 0.8rem;

}

form{
    display: flex;
    flex-direction: column;
    grid-gap: 1.2rem;
    gap: 1.2rem;
}

input,textarea{
    width: 100%;
    padding: 1.5rem;
    border-radius: 1.5rem;
    background: transparent;
    border: 2px solid var(--color-primary-variant);
    resize: none;
    color: white;

}

/*-------------------------- Media for Tablets---------------------------*/

@media screen and (max-width:1024px) {
    .contact_container{
        grid-template-columns: 1fr;
        grid-gap: 2rem;
        gap: 2rem;
    }
}

/*-------------------------- Media for Mobiles---------------------------*/

@media screen and (max-width:600px) {
    .contact_container{
        grid-template-columns: 1fr;
        grid-gap: 2rem;
        gap: 2rem;
    }
}
